  position: relative;
}
.fws-quiz .quiz__media {
  overflow: hidden;
  position: relative;
}
.fws-quiz .quiz__media:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("/modules/custom/fws_counting/images/Plane_Overlay.png") no-repeat center center;
  background-size: cover;
  z-index: 100;
}
.fws-quiz .quiz__media img {
  width: 100%;
  animation: aerialView 15s linear 1 forwards;
  transform-origin: center center;
  will-change: transform;
  transition: transform 0.5s linear;
  aspect-ratio: 16/10;
  object-fit: cover;
  z-index: 50;
}
